module.exports = "/* \"X\" çıkış düğmesi stilini özelleştirme */\r\n.iptal {\r\n  background-color: #671786;\r\n  color: #fff;\r\n  border: none;\r\n  border-radius: 50%;\r\n  width: 30px;\r\n  height: 30px;\r\n  font-size: 15px;\r\n  line-height: 1;\r\n  display: flex;\r\n  align-items: center; /* Düğme içeriğini dikey hizala */\r\n  justify-content: center; /* Düğme içeriğini yatayda ortala */\r\n  cursor: pointer; /* Üzerine gelindiğinde imleci bir el işareti yap */\r\n  transition: background-color 0.3s ease; /* Arka plan rengi için yumuşak bir geçiş ekleyin */\r\n  position: relative; /* İçeriğin konumunu belirlemek için */\r\n}\r\n.iptal i {\r\n  position: absolute; /* İçeriği mutlak konumlandırma yapmak için */\r\n  top: 50%; /* Dikeyde %50 */\r\n  left: 50%; /* Yatayda %50 */\r\n  transform: translate(-50%, -50%); /* İçeriği yuvarlak düğmenin merkezine kaydırma */\r\n}\r\n.iptal:hover {\r\n  background-color: #995ea8;\r\n}\r\n/* \"Hoş Geldiniz\" metni stilini düzenleme */\r\n#welcome-text {\r\n  color: #000000; /* Hoşgeldiniz yazısının rengini ayarlayabilirsiniz */\r\n  font-weight: bold; /* Yazının kalınlığını artırabilirsiniz */\r\n  text-align: right; /* Metni sağa yaslayın */\r\n}\r\n#welcome-text-container {\r\n  background-color: #7278AD; /* Kutu arka plan rengi */\r\n  padding: 10px; /* Kutu içeriği ile kenarları arasındaki boşluk */\r\n  border-radius: 5px; /* Kutunun kenarlarının yuvarlaklığı */\r\n  display: inline-block; /* Kutunun içeriğe uygun boyutlanmasını sağlar */\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"