module.exports = "/* Arama alanı stilini özelleştirme */\r\n.custom-search {\r\n    background-color: #ccc;\r\n    border-radius: 5px;\r\n    padding: 10px;\r\n    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);\r\n  }\r\n/* Tablo stilleri */\r\n.table {\r\n    background-color: #f5f5f5; /* Tablo arka plan rengi */\r\n    border: 1px solid #ddd; /* Tablo kenarlığı */\r\n    border-collapse: collapse;\r\n    width: 100%;\r\n  }\r\n/* Tablo başlık stilleri */\r\n.table thead {\r\n    background-color: #2b0e35; /* Başlık arka plan rengi */\r\n    color: #fff; /* Başlık metin rengi */\r\n  }\r\n/* Tablo başlık hücreleri */\r\n.table th {\r\n    padding: 12px; /* Başlık içeriği için iç boşluk */\r\n    text-align: center; /* Başlık metni ortala */\r\n    background-color: darkslategray;\r\n    color: white;\r\n  }\r\n/* Tablo veri hücreleri */\r\n.table td {\r\n    padding: 10px; /* Hücre içeriği için iç boşluk */\r\n    text-align: center; /* Hücre metni ortala */\r\n    border-top: 1px solid #ddd; /* Hücre üst kenarlığı */\r\n  }\r\n/* Zebra çizgili tablo (opsiyonel) */\r\n.table tbody tr:nth-child(even) {\r\n    background-color: #e0e0e0; /* Zebra çizgili arka plan rengi */\r\n  }\r\n/* Hücre içeriği stilini özelleştirme (örnek olarak) */\r\n.table td {\r\n    color: #333; /* Hücre metin rengi */\r\n    background-color: #f5f5f5; /* Hücre arka plan rengi */\r\n  }\r\n/* Seçili satır stilini özelleştirme (opsiyonel) */\r\n.table tbody tr.selected {\r\n    background-color: #ffffcc; /* Seçili satırın arka plan rengi */\r\n  }\r\n/* \"Sil\" düğmesi stilini özelleştirme */\r\n.action-button {\r\n    margin: 5px;\r\n    padding: 10px 20px;\r\n    font-size: 16px;\r\n    border-radius: 5px;\r\n    cursor: pointer;\r\n    transition: background-color 0.3s;\r\n  }\r\n.action-button:disabled {\r\n    background-color: #ccc;\r\n    cursor: not-allowed;\r\n  }\r\n.add-button {\r\n    background-color: #7278ad;\r\n    color: #fff;\r\n  }\r\n.delete-button {\r\n    background-color: #d31500;\r\n    color: #fff;\r\n  }\r\n.edit-button {\r\n    background-color: #3498db;\r\n    color: #fff;\r\n  }\r\n.report-button {\r\n    background-color: #17b83f;\r\n    color: #fff;\r\n  }\r\n.action-button:hover {\r\n    filter: brightness(1.2);\r\n  }\r\n/* Taşınmaz bulunamadı mesajı stilini özelleştirme */\r\n.no-data-message {\r\n    text-align: center; /* Metni ortala */\r\n    font-size: 18px; /* Metin boyutu */\r\n    margin-top: 20px; /* Üst boşluk */\r\n    padding: 10px; /* İç boşluk */\r\n    background-color: #fff; /* Arka plan rengi */\r\n    border: 1px solid #ddd; /* Kenarlık */\r\n    border-radius: 5px; /* Köşe yuvarlama */\r\n    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Gölge efekti */\r\n  }\r\n:host {\r\n    background-color: #2b0e35; /* Arka plan rengi (örnek olarak mavi) */\r\n    background-size: cover;\r\n    background-repeat: no-repeat;\r\n    background-attachment: fixed;\r\n  }\r\n/*# sourceMappingURL=data:application/json;base64, */"